<template>
  <div>
    <!-- UI按钮暂停 -->
    <div id="rotate"><img id="img" style="" src="@/assets/旋转.png"></div>
    <!-- 颜色器交互 -->
    <div id="color" >
        <div class="colorChoose" id="map1"><img src="@/assets/极光紫.png"></div>
        <div class="colorChoose" id="map2"><img src="@/assets/幻夜黑.png"></div>
        <div class="colorChoose" id="map3"><img src="@/assets/珊瑚红.png"></div>
        <div class="colorChoose" id="map4"><img src="@/assets/极光蓝.png"></div>
      </div>
      <!-- 相机交互 -->
      <div id="camera" style="visibility:hidden;">
        <div>
          <div style="height:1px;width:130px;background: #00ffff;margin-top:68px"></div>
        </div>
        <div id="message" style="width:350px;height:175px;">
          <div style="padding: 10px 4px;font-size:18px;">双摄像头</div>
          <div style="padding: 10px 24px;font-size:16px;">后置主摄像头——1300万像素(F/1.8光圈)</div>
          <div style="padding: 10px 24px;font-size:16px;">后置副摄像头——200万像素的</div>
        </div>
        <!-- 设置一个关闭按钮 -->
        <div style="position:relative;">
          <div style="position: absolute;left: -30px;top: 10px;">
            <img id="close" src="@/assets/关闭.png" alt="" width="18" style="cursor: pointer;">
          </div>
        </div>
      </div>
  </div>
</template>

<script>
// 引入Three.js
import * as THREE from "three/build/three.module.js";
// 渲染结果
import {
  rotateAnimation, // 开始动画
  stopRotateAnimaiton, // 停止动画
  renderer
} from '../threejs/RenderLoop'
// 手机网格模型Mesh
import { mobilePhoneMesh } from '../threejs/scene/model'
// 射线拾取
import { choose } from '../threejs/choose'
import { labelRenderer } from '../threejs/RendererCamera'
// 素材
import stopRotatePNG from '@/assets/停止旋转.png'
import RotatePNG from '@/assets/旋转.png'
    
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Mobile',
  props: {
    msg: String
  },
  mounted: function () {
    // 设置颜色控制器位置
    var div = document.getElementById('color');
    div.style.left = (window.innerWidth - 314) / 2 + 'px';

    // 设置旋转开关位置
    var div2 = document.getElementById('rotate');
    div2.style.left = (window.innerWidth - 60) / 2 + 'px';
    
    // Three.js渲染结果Canvas画布插入到body元素中
    document.body.appendChild(renderer.domElement);

    // 热点事件监听
    addEventListener("click", choose); // 监听窗口鼠标单击事件

    // 关闭监听事件
    this.closeEvent();

    // 颜色监听
    this.colorEvent();

    // 旋转监听
    this.rotateEvent();
  },
  methods: {
    closeEvent: function () {
    var close = document.getElementById('close');
    // 点击关闭按钮隐藏HTML元素标签
    close.addEventListener('click', function () {
      labelRenderer.domElement.style.display = "none";
    })
    },
    rotateEvent: function () {
        const div = document.getElementById('rotate');
        const img = document.getElementById('img');
        let rotateBool  = true;
        div.onclick = function () {//按钮单击停止或启动旋转动画
          if(rotateBool){
            stopRotateAnimaiton();//手机旋转展示动画停止

            img.src=stopRotatePNG;//切换按钮图表
          }else{
            rotateAnimation();//手机旋转展示
            img.src=RotatePNG;//切换按钮图表
          }
            rotateBool = !rotateBool;      
        }
    },
    colorEvent: function () {
          var texLoader = new THREE.TextureLoader();//纹理贴图加载器
          
          var map1 = texLoader.load("./model/极光紫.png");//颜色贴图    
          var map2 = texLoader.load("./model/幻夜黑.png");//颜色贴图
          var map3 = texLoader.load("./model/珊瑚红.png");//颜色贴图
          var map4 = texLoader.load("./model/极光蓝.png");//颜色贴图
          map1.flipY = false;// 纹理朝向texture.flipY
          map2.flipY = false;// 纹理朝向texture.flipY
          map3.flipY = false;// 纹理朝向texture.flipY
          map4.flipY = false;// 纹理朝向texture.flipY


    
          var dom1 = document.getElementById('map1');
          var dom2 = document.getElementById('map2');
          var dom3 = document.getElementById('map3');
          var dom4 = document.getElementById('map4');

          var chooseDom = dom1;
          // 每个手机颜色切换按钮设置一个鼠标单击事件
          dom1.onclick = function () {
            chooseDom.style.borderColor = "#ccc";
            chooseDom = dom1;
            chooseDom.style.borderColor = "#00aaaa";
            mobilePhoneMesh.material.map = map1;
          }
          dom2.onclick = function () {
            chooseDom.style.borderColor = "#ccc";
            chooseDom = dom2;
            chooseDom.style.borderColor = "#00aaaa";
            mobilePhoneMesh.material.map = map2;
          }
          dom3.onclick = function () {
            chooseDom.style.borderColor = "#ccc";
            chooseDom = dom3;
            chooseDom.style.borderColor = "#00aaaa";
            mobilePhoneMesh.material.map = map3;
          }
          dom4.onclick = function () {
            chooseDom.style.borderColor = "#ccc";
            chooseDom = dom4;
            chooseDom.style.borderColor = "#00aaaa";
            mobilePhoneMesh.material.map = map4;
          }
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    /* 颜色器 */
    #color {
      width: 314px;
      position: absolute;
      /* top: 30px; */
      bottom: 12px;
      /* background:rgba(255,255,255,0.1); */
    }
    .colorChoose {
      display: inline-block;
      margin-left: 20px;
      cursor: pointer;
    }
    .colorChoose img {
      width: 50px;
    }
    
    /*  热点标注 */
    #camera {
      position: absolute;
      display: flex;
      /* display: none; */
      /* row是flex-direction默认值,可以不设置，表示主轴为水平方向，从左向右排列*/
      flex-direction: row;
      /*space-between表示*/
      justify-content: space-between;
    }

    #message {
      color: #fff;
      background: rgba(0, 0, 0, 0.5);
      padding: 0px;
      /* 边框 */
      background: linear-gradient(#00ffff, #00ffff) left top,
        linear-gradient(#00ffff, #00ffff) left top,
        linear-gradient(#00ffff, #00ffff) right bottom,
        linear-gradient(#00ffff, #00ffff) right bottom;
      background-repeat: no-repeat;
      background-size: 1px 20px, 36px 1px;
      background-color: rgba(255, 255, 255, 0.1);
      color: #ffffff;
      font-size: 18px;
      padding: 8px 12px;
    }

    /* 旋转UI按钮样式 */
    #rotate {
      position: absolute;
      /* top: 30px;*/
      bottom: 60px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 30px;
      padding: 12px;
    }
    #rotate img{
      cursor: pointer;
      width:26px;
    } 
</style>
